.benefits__title {
    font-weight: bold;
    font-size: 18px;
    line-height: 1.33;
    margin: 0;
    color: #000000;
}

.benefits__text {
    margin: 0;
}

.benefits__list {
    .list-reset();
    background-color: #f2f2f2;

    &::after {
        display: none;
        content: "";
        width: 157px;
        height: 320px;
        background-image: url("..img/iphone-screen-mobile@1x.png");
        background-repeat: no-repeat;
        position: absolute;
    }
}

.benefits__slogan {
    .section-heading();
    margin: 0;
    position: relative;
}