.main-nav__link {
    color: #ffffff;
    font-weight: bold;
    font-size: 18px;
    line-height: 1.77;
    text-transform: uppercase;
}

.main-nav__list {
    .list-reset();
    background-color: #283645;
}

.main-nav__item {
    background-color: #283645;
    box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(#ffffff, 0.15);
    border-bottom: 1px solid #000000;
}
